import React from "react";
import safe from "./staysafe.png";

function Home() {
  return (
    <div>
      <section id="showcase">
        <div className="container">
          <h1>
            <p>Through my eyes,</p>
            <p>Living my life,</p>
            <p>Coding with me and</p>
            <p>Welcome to my world</p>
          </h1>
        </div>
      </section>
      <div class="container-1">
        <div class="box-1">
          <h1>What brings you here?</h1>
          <p>{new Date().toLocaleString()}</p>
          <p>Please keep social distance and stay Safe.</p>
          <p>
            <button
              onClick={() => {
                window.open(
                  "https://qunliw.github.io/meme-generator/",
                  "Popup",
                  "toolbar=no, location=no, statusbar=no, menubar=no, scrollbars=1, resizable=0, width=580, height=600, top=30"
                );
              }}
            >
              Play a fun Meme Generator Game!
            </button>
          </p>
        </div>
        <div class="box-2">
          <img src={safe} alt="Stay safe" />
        </div>
      </div>
    </div>
  );
}

export default Home;
